@font-face {
  font-family: 'CenturyGothic';
  src: url('../fonts/CenturyGothic.woff2') format('woff2'), url('../fonts/CenturyGothic.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'CoreSans';
  src: url('../fonts/CoreSansE-35Light.woff2') format('woff2'), url('../fonts/CoreSansE-35Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Haettenschweiler';
  src: url('../fonts/Haettenschweiler.woff2') format('woff2'), url('../fonts/Haettenschweiler.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

body {
  font-size: 20px;
  font-family: 'CoreSans', sans-serif;
  background-color: #141212;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

abbr {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.accent-text {
  color: #5b8b2d;
}

.main-header {
  padding: 5px 0;
  font-family: 'CenturyGothic', sans-serif;
  font-size: 16px;
  color: #92d5bc;
  background: #050505 url(../img/header-bd.jpg) 0 0 no-repeat;
  background-size: cover;
}

.main-header .container {
  display: flex;
  justify-content: space-between;
}

.main-header--logo {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: auto;
  height: 30px;
  margin-top: auto;
  margin-bottom: auto;
}

.main-header--search {
  width: 450px;
  height: auto;
  background-color: #fff;
}

.contacts--item {
  margin-bottom: 6px;
}

.contacts--link {
  position: relative;
  color: #92d5bc;
}

.contacts--link::before {
  content: '';
  position: absolute;
  top: 50%;
  left: -30px;
  transform: translateY(-50%);
}

.contacts--link__e-mail::before {
  width: 23px;
  height: 17px;
  background: url(../../img/mail.png) center no-repeat;
  background-size: cover;
}

.contacts--link__phone::before {
  width: 20px;
  height: 21px;
  left: -27px;
  background: url(../../img/phone.png) center no-repeat;
  background-size: cover;
}

.contacts--link__location::before {
  width: 17px;
  height: 23px;
  left: -25px;
  background: url(../../img/location.png) center no-repeat;
  background-size: cover;
}

.discounts {
  margin-bottom: 10px;
}

.discounts .container {
  position: relative;
}

.discounts--wrap {
  position: relative;
}

.discounts--list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.discounts--item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% - 10px);
  margin: 0 auto;
  display: none;
  position: relative;
  overflow: hidden;
}

.discounts--item__active {
  display: block;
}

.discounts--header {
  font-size: 50px;
  font-weight: 700;
  color: #232e30;
  margin-top: 100px;
  margin-left: 100px;
}

.discounts--img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.discounts--info {
  margin-top: 30px;
  margin-bottom: 50px;
  padding: 10px 100px;
  color: #95b1b5;
  background-color: rgba(0, 0, 0, 0.5);
}

.discounts--btns {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.discounts--btn {
  font-size: 0;
  border: none;
  padding: 0;
  width: 27px;
  height: 52px;
  background: url(../img/arrow.png) center no-repeat;
}

.discounts--btn__prev {
  transform: rotate(180deg);
}

.discounts--checks {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  justify-content: space-between;
  width: 70px;
  margin-bottom: 10px;
}

.discounts--check {
  font-size: 0;
  border: none;
  padding: 0;
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 2px solid #8BBEAD;
  border-radius: 50%;
}

.services--list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

.services--item {
  position: relative;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(50% - 10px);
  margin-bottom: 10px;
  background-color: #99ce94;
}

.services--item__long {
  width: calc(100% - 10px);
}

.services--item__long .service--item {
  width: calc(16.666666666666668% - 10px);
}

.services--header {
  font-size: 27px;
  color: #394d44;
  background-color: #99ce94;
}

.services--header__left {
  margin-right: 20px;
  padding-right: 5px;
  text-align: right;
}

.services--header__right {
  margin-left: 20px;
  padding-left: 5px;
  text-align: left;
}

.services--header__long {
  text-align: center;
}

.services--insure {
  color: #192822;
}

.services--bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 100;
  opacity: 0.5;
}

.services--bg__left {
  background-image: url(../img/services-left-bg.png);
}

.services--bg__right {
  background-image: url(../img/services-right-bg.png);
}

.services--bg__long {
  background-image: url(../img/services-long-bg.png);
}

.services--wrap {
  padding-top: 35px;
  padding-bottom: 85px;
  background-color: #588b52;
}

.service--list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-around;
}

.service--item {
  width: 30%;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.service--item:first-child {
  margin-left: 20px;
}

.service--item:last-child {
  margin-right: 20px;
}

.service--header {
  margin-top: -65px;
  margin-bottom: 5px;
  padding-bottom: 30px;
  font-family: 'Haettenschweiler', sans-serif;
  font-size: 32px;
  color: #192822;
  background-color: #588b52;
}

.service--about {
  flex-grow: 1;
  margin-bottom: 15px;
  font-size: 18px;
  color: #394d44;
}

.service--price {
  margin-bottom: 5px;
  font-size: 14px;
  color: #192822;
  font-weight: bold;
}

.service--link {
  margin-bottom: 10px;
}

.about-us--wrap {
  max-width: 1400px;
  margin: 0 auto;
  padding: 1px;
  padding-bottom: 200px;
  background: url(../img/about-us-bg.jpg) center no-repeat;
  background-size: cover;
  border-bottom-right-radius: 75px;
  border-bottom-left-radius: 75px;
}

.about-us--header {
  margin-left: 47%;
  margin-bottom: 35px;
  margin-top: 70px;
  font-size: 48px;
  font-family: 'CenturyGothic', sans-serif;
  color: #161e1b;
}

.about-us--info {
  margin-left: 47%;
  margin-bottom: 100px;
  padding-right: 70px;
  font-size: 23px;
  font-family: 'CenturyGothic', sans-serif;
}

.faq {
  margin-top: -170px;
  margin-bottom: 100px;
  font-family: 'CenturyGothic', sans-serif;
  font-size: 48px;
  color: #161e1b;
}

.faq--wrap {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px 0;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.42);
}

.faq--header {
  display: inline-block;
}

.faq--link {
  color: #446822;
}

.faq--text {
  display: inline-block;
}

.main-footer {
  padding-bottom: 30px;
  font-family: 'CenturyGothic', sans-serif;
  font-size: 18px;
  color: #35521a;
}

.main-footer--wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  box-sizing: border-box;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

.main-footer--list {
  margin-left: 100px;
}

.main-footer--item {
  margin-bottom: 8px;
}

.main-footer--link {
  color: #35521a;
  border-bottom: 1px solid #35521a;
}

.main-footer--asks {
  font-size: 36px;
}

.main-footer--phone {
  display: block;
  margin-bottom: 20px;
  padding-right: 45px;
  font-size: 36px;
  color: #35521a;
  text-align: right;
}

.main-footer--address {
  text-align: right;
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}